div.btns{
  position: relative;
  top: 2%;
  height: 50%;
  button{
    position: relative;
    width: 25%;
    height: 20%;
    transition: all 0.1s;
    img{
      display: inline-block;
      height: 60%;
    }
  }//button
  /* 클릭모션 */
  &:active{
    button{
      transform: scale(0.8);
      &:active{
        z-index: 1;
        transform: scale(2);
      }
    }
  }
}//div.btns