@charset "UTF-8";
div.btns {
  position: relative;
  top: 2%;
  height: 50%;
  /* 클릭모션 */
}
div.btns button {
  position: relative;
  width: 25%;
  height: 20%;
  transition: all 0.1s;
}
div.btns button img {
  display: inline-block;
  height: 60%;
}
div.btns:active button {
  transform: scale(0.8);
}
div.btns:active button:active {
  z-index: 1;
  transform: scale(2);
}