.sorry{
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(#000,0.8);
  &.active{
    opacity: 0;
    transition: opacity 1s;
  }
  img{
    position: absolute;
    max-width: 70%;
    max-height: 70%;
    animation: tada 1s 2;
  }
}