div.output {
  position: relative;
  height: 40%;
}
div.output::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 120%;
  bottom: 0;
  background-image: url(../img/output.png);
  background-size: 100% 100%;
}
div.output p {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 11%;
  left: 6.5%;
  width: 87%;
  height: 78%;
  box-sizing: border-box;
  padding: 5px;
}
div.output p img {
  width: 10%;
  animation: swing 1s;
}