body{
  background-image: url(../img/paper.jpg);
  background-size: cover;
 }

.app{
  position: fixed;
  left: 50%; top: 50%;
  transform: translate(-50%,-50%);
  width: 95%;
  height: 95%;
  max-width: 400px;
  max-height: 600px;
  border: 1px solid olive;
  &::before{
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    background-image: url(../img/bg.png);
    background-size: 100% 100%;
    transform: scale(1.2);
  }
  >*{ /* title,output,btns */
    position: relative;
  }
}